<template>
    <pdd-layout
        page="shops"
        :bottom-text="bottomText">
        <div class="shops-conten-placeholder"/>
        <div class="shops-content">
            <van-list
                v-model="loading"
                :finished="finished"
                @load="getMerchantList"
            >
                <div
                    v-for="(item,index) in shoplist"
                    @click="goShopDetail(item)"
                    :key="index"
                    class="shops">
                    <div class="shop-title">
                        <img
                            v-lazy='item.img_url'
                            class="shop-img">
                        <div class="shop-info">
                            <div class="shop-name-tag">
                                <span class="shop-name">{{ item.mallName }}</span>
                                <span class="shop-tag">{{ merchantType[item.merchantType] }}</span>
                            </div>
                            <div class="shop-desc">
                                描述相符：高 | 服务态度： 高 | 物流服务：高
                            </div>
                            <div class="shop-sell">
                                近30天销量： {{ item.salesTip }}
                            </div>
                        </div>
                    </div>
                    <div class="shop-good-list">
                        <div
                            v-for="(it,ind) in item.list.slice(0,3)"
                            :key="ind"
                            class="shop-good">
                            <img v-lazy='it.imgUrl'>
                            <div class="shop-good-info">
                                <div class="shop-good-name">
                                    {{ it.name }}
                                </div>
                                <div class="shop-good-price">
                                    活动价:
                                    <span>￥{{ it.computePrice }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="shop-btn">
                        进店逛逛
                    </div>
                </div>
            </van-list>
        </div>

    </pdd-layout>
</template>

<script>
import { getMerchantList } from '@/api/pddMall';
import PDDLayout from './components/PDDLayout';
import { computePrice } from './config';

const page = 'PDD商铺列表页';
const screen = '第二屏';

const merchantType = {
    1: '个人',
    2: '企业',
    3: '旗舰店',
    4: '专卖店',
    5: '专营店',
    6: '普通店',
};

export default {
    name: 'PDDMallShops',
    components: {
        pddLayout: PDDLayout,
    },
    created() {
        this.$o2o({
            e_c: '店铺首页',
            e_a: '曝光',
            e_n: page,
        });
        this.$root.hidePlaceholder();
    },
    data() {
        return {
            shoplist: [],
            bottomText: '',
            merchantType,
            page: 1,
            pageSize: 10,
            loading: false,
            finished: false,
            finishedText: '',
        };
    },
    methods: {
        getMerchantList() {
            this.loading = true;
            getMerchantList({
                page: this.page,
                pageSize: this.pageSize,
            }).then((data) => {
                this.page += 1;
                this.loading = false;
                if (!data.length) {
                    this.finished = true;
                    this.bottomText = '- 今日热推好店都在上面啦 -';
                }
                data.forEach((item) => {
                    item.list.slice(0, 3).forEach((it) => {
                        // eslint-disable-next-line no-param-reassign
                        it.computePrice = this.computePrice(it.originalPrice, it.discount);
                    });
                });
                this.shoplist = Object.freeze([...this.shoplist, ...data]);
            }).catch(() => {
                this.$error();
            });
        },
        goShopDetail(shop) {
            this.$o2o({
                e_c: '店铺',
                e_a: '点击',
                e_n: page,
                e_x: {
                    name: shop.mallName,
                    path: screen,
                },
            });
            this.$router.push({
                name: 'pddMallShopsDetail',
                params: {
                    shop,
                },
            });
        },
        computePrice(originalPrice, discount) {
            return computePrice(originalPrice, discount);
        },
    },
};
</script>

